const { getStarWarsPerson } = require('./api')

const App = (function() {
	const App = function() {
		// Application State
		this.state = {
			people: []
		}
	}

	App.prototype = {
		getPerson: function(personId, fn) {
			fn(personId)
				.then(res => res.json())
				.then(data => this.state.people.push(data))
		},
		render: function() {
			this.getPerson(22, getStarWarsPerson)
			return `
                <h1>Hello World!</h1>
                ${console.log(this.state)}
            `
		}
	}

	return App
})()

const entry = document.querySelector('#app')
const app = new App()

entry.innerHTML = app.render()
